(function () {
    'use strict';

    angular
        .module('app')
        .controller('PersonController', PersonController);

    /** @ngInject */
    function PersonController(item, $mdDialog, qs) {
        
        var vm=this, modal, query, postData;
        
        
        vm.item = item;
        
        
        vm.close = function() { $mdDialog.hide(); }
        
        vm.confirm = function( item ) {
            postData = angular.copy(item);
            delete(postData.isContact);
            
            postData.content = angular.toJson(item.content)
           
            if ( angular.isUndefined(item.id) ) {
                console.log("create", postData);
                query = qs.create('persoane', postData);
            } else {
                 console.log("update", postData);
                query = qs.update('persoane', postData);
            }
            query.then( function(resp) { 
                if ( resp.ok = true ) {
                    console.log("resp", resp);
                    item.id = resp.data.msg;
                    console.log("item after query",item);
                    $mdDialog.hide(item);
                }
            })
            
        }
       
        
      
        
        
     

    }


})();
