(function () {
    'use strict';

    angular
        .module('app')
        .controller('contactsController', contactsController)
    

    /** @ngInject */
    function contactsController(contacts, $stateParams, $state, qs ) {
        var vm = this;
		
		if (typeof contacts[0] === "object" ) {
			angular.forEach(contacts, function(e,i) {
				contacts[i].content = angular.fromJson(e.content);
			})
		} else {
			contacts.content = angular.fromJson(contacts.content);
		}
		
		vm.contacts = contacts;
		vm.action = $stateParams.action;
		
		//select the coresponding form based on field existance
		
		vm.stateGoUpdate = function(item) {
			if ( item.cnp  ) { $state.go('index.addressBook', {action:'update-pf', id: item.id })}
			if ( item.cui  ) { $state.go('index.addressBook', {action:'update-pj', id: item.id })}
		}
		
		
//		adaugare
		vm.add = function(data, personType) {
			console.log("add:",data);
			data = angular.copy(data);
            console.log("data content:",typeof data.content, data.content);
			angular.isUndefined(data.content) ? data.content = {} : data.content=data.content;
            data.content = angular.toJson(data.content);
			console.log(data);
			qs.create('persoane', data)
			.then(function(r) { 
				$state.go('index.addressBook', {action:'update-'+personType, id: r.data.msg});	
			})
		}
		
		vm.update = function(type, data) {
			console.log("update:",data);
			var post = angular.copy(data);
			post.content = angular.toJson(post.content);
			qs.update('persoane', post)
			.then(function(r) {
				$state.go('index.addressBook', {action:'', id:''});
			})
			console.log("update:",data);
		}
		
		vm.remove = function(item) { 
			qs.remove('persoane', {id:item.id})
			.then(function() {
				$state.go('index.addressBook', {action:'', id: ''} )
			})
		}
		
		
    }
        
    


})();
