$templateCache.put("app/states/dosar/includes/pv4cu.tpl","<md-toolbar class=\"md-accent md-hue-4 md-whiteframe-z3\"><div class=md-toolbar-tools><h5>Proces verbal final (cu preemptori) pentru dosarul nr. <b>{{vm.dosar.nr}}</b> din <b>{{ vm.dosar.date | date:\"dd.MM.yyyy\"}}</b></h5><span flex></span></div></md-toolbar><md-content ng-if=!vm.dosar.content.ppc layout-align=\"center center\" style=text-align:center><h1 style=color:red>NU PUTEȚI GENERA ÎNCĂ PROCESUL VERBAL FINAL !<br><h4>-- nu a fost ales preemptorul potențial cumpărător --</h4><br></h1></md-content><md-content ng-if=vm.dosar.content.ppc style=\"text-align: justify;text-justify: inter-word\"><!--nr inregistrare--><div layout=row flex layout-padding><span flex></span><!--            <span ng-init = \"vm.dosar.content.pvFinalCu = vm.dosar.content.pvFinalCu ? vm.dosar.content.pvFinalCu : []\" flex></span>--> <span ng-init=\"vm.dosar.content.pvFinalCu.nr = vm.dosar.content.pvFinalCu.nr ?  vm.dosar.content.pvFinalCu.nr :  vm.dosar.nr\"></span> <span ng-init=\"vm.dosar.content.pvFinalCu.date = vm.dosar.content.pvFinalCu.date ?  vm.dosar.content.pvFinalCu.date :  vm.dosar.date\"></span> <small><md-input-container><label>Nr.inregistrare</label><input ng-model=vm.dosar.content.pvFinalCu.nr aria-label=\"nr 3a\"></md-input-container><md-input-container><label>Data</label><md-datepicker ng-model=vm.dosar.content.pvFinalCu.date aria-label=\"data 3a\"></md-datepicker></md-input-container></small></div><br><div layout=row><span flex></span><h4 style=text-align:center>PROCES-VERBAL FINAL<br>nr. {{vm.dosar.content.pvFinalCu.nr}} din {{vm.dosar.content.pvFinalCu.date|date:\"dd.MM.yyyy\"}}<br>de constatare a derulării fiecărei etape procedurale prevăzute la art.7 din legea nr.17/2014 modificată,<br>întocmit pentru<br>CEREREA ŞI OFERTA DE VÂNZARE NR. <b>{{vm.dosar.nr}}</b> DIN <b>{{ vm.dosar.date | date:\"dd.MM.yyyy\"}}</b><br>depuse de <span ng-repeat=\"p in vm.dosar.persoane\">{{p.titular.name}} <span ng-if=\"$index != vm.dosar.persoane.length-1\">,</span> </span><span>{{vm.dosar.firma.name}}</span></h4><span flex></span></div><div layout=column><p>In temeiul art. 9 si 10 din Legea nr. 17/2014, privind unele masuri de reglementare a vânzări-cumpărării terenurilor agricole situate in extravilan și de modificare a Legii nr.268/2001 privind privatizarea societăţilor comerciale ce deţin în administraţie terenuri proprietate publică și privată a statului cu destinaţie agricolă și înfiinţarea Agenţiei Domeniului Statului, cu modificările ulterioare, având în vedere oferta de vânzare depusă de <span ng-include=\"\'app/states/dosar/includes/_subsemnatii.tpl\'\"></span> și înregistrată la Primăria <strong>{{vm.local.company.cityName}}</strong>, având sediul în localitatea <strong>{{vm.local.company.cityName}}</strong>, <strong>{{vm.local.company.address}}</strong> sub numărul <strong>{{vm.dosar.nr}} </strong>din data de <strong>{{vm.dosar.date|date:\"dd.MM.yyyy\"}}</strong></p><p>Ca urmare a verificării modului de respectare a procedurii prevăzuta de lege privind exercitarea dreptului de preempțiune în vederea încheierii contractului de vânzare - cumpărare în formă autentică de către notarul public, în urma publicității, <span ng-show=\"vm.dosar.content.acceptari.length == 1\">s-a prezentat un singur preemptor,</span> <span ng-show=\"vm.dosar.content.acceptari.length > 1\">s-au prezentat următorii preemptori</span> <span ng-repeat=\"pre in vm.dosar.content.acceptari\"><strong>{{pre.persoana.name|uppercase}}</strong> în calitate de <strong>{{pre.type}}</strong> a cărui comunicare de acceptare a ofertei de vânzare a fost înregistrată sub nr. <strong>{{pre.nr}}</strong> din data de <strong>{{pre.date|date:\"dd.MM.yyyy\"}} </strong><span ng-show=\"$index != vm.dosar.content.acceptari.length-1\">,</span></span></p><p>Preemptorul potențial cumpărător ales de către vânzător este <strong>{{vm.dosar.content.ppc.preemptorAles.persoana.name}}</strong> în calitate de <strong>{{vm.dosar.content.ppc.preemptorAles.type}} </strong>al terenului agricol situat în extravilanul localității <strong>{{vm.dosar.content.oferta.loc}}</strong>, judeţul <strong>{{vm.dosar.content.oferta.jud}}</strong>, nr. tarla <strong>{{vm.dosar.content.oferta.nrTarlaLot}}</strong>, parcela <strong>{{vm.dosar.content.oferta.nrParcela}}</strong>, identificat cu nr. cadastral <strong>{{vm.dosar.content.oferta.nrCadastral}}</strong>, înscris in cartea funciară nr. <strong>{{vm.dosar.content.oferta.nrCF}}</strong>, a localității <strong>{{vm.dosar.content.oferta.loc}}</strong>, în suprafaţa de <strong>{{vm.dosar.content.oferta.ha}}</strong>, la preţul de vânzare de <strong>{{vm.dosar.content.oferta.pret}} lei</strong>.</p><p>Precizăm pe această cale și faptul că au fost parcurse absolut toate etapele procedurale impuse de prevederile Legii nr. 17/2014 privind unele masuri de reglementare a vânzării-cumpărării terenurilor agricole situate în extravilan, a Ordinului M.A.D.R. nr. 719/12.05.2014, a Ordinului M.Ap.N. nr. 57 / 13.05.2014, a Ordinului M.D.R.A.P. nr. 740/13.05.2014, precum și prevederile Ordinului Ministerului Culturii nr. 2333 / 13.05.2014.</p><br><br>{{vm.local.company.cityName}}, la {{vm.dosar.content.pvFinalCu.date | date: \"dd.MM.yyyy\"}}<br><br><div layout=row style=\"font-size: 14px\" flex><span ng-include=\"\'app/states/dosar/_semnaturi_titulari.tpl\'\"></span> <span flex></span> <span layout=column layout-align=\"start start\"><a>Cumpărător,</a> <strong>{{vm.dosar.content.ppc.preemptorAles.persoana.name}}</strong> <span layout=column ng-show=vm.dosar.content.ppc.preemptorAles.mandatar.name><span>prin </span><strong>{{vm.dosar.content.ppc.preemptorAles.mandatar.name}}</strong> <span>în calitate de</span> {{vm.dosar.content.ppc.preemptorAles.mandat.calitateDe}} </span></span><span flex></span> <span ng-include=\"\'app/states/dosar/_semnaturi.tpl\'\"></span></div></div></md-content><!--<pre>{{vm.dosar.content|json}}</pre>-->");
